import styled from 'styled-components';
import { colors } from '../../../utils/colors';
import { device } from '../../../utils/layoutBreakpoints';

export const TalkToAnExpertFormContainer = styled.div`
  display: flex;
  background-color: ${colors.secondary.gray100};
  justify-content: space-between;
  align-items: center;
  text-align: center;
  padding: 50px 0;

  h2 {
    margin-bottom: 30px;
    font-weight: 600;
  }

  @media ${device.laptop} {
    padding: 100px 0;
  }
`;

export const StyledTalkToAnExpertForm = styled.div`
  font-size: 0.8rem;
  line-height: 1.1rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  gap: 15px;
  width: 100%;
  label {
    font-weight: 500;
    display: flex;
    flex-direction: column;
    gap: 5px;
    width: 100%;
  }

  input,
  textarea {
    width: 100%;
    box-sizing: border-box;
    background-color: ${colors.primary.white};
    color: ${colors.primary.black};
    border: 1px solid ${colors.secondary.gray300};
    font-family: 'Rubik';
    border-radius: 5px;
    &::placeholder {
      color: ${colors.primary.gray500};
      font-family: 'Rubik';
    }
    &:focus {
      outline: none;
      border: 1px solid ${colors.primary.blue};
    }
  }

  input {
    height: 36px;
    padding: 10px 15px;
  }

  textarea {
    height: 100px;
    padding: 10px 15px;
    resize: none;
  }

  fieldset {
    margin-bottom: 30px;
    display: flex;
    flex-direction: column;
    width: 100%;
    gap: 10px;
    legend {
      font-weight: 500;
      margin-bottom: 10px;
    }
    label {
      cursor: pointer;
      background: ${colors.primary.white};
      border: 1px solid ${colors.secondary.gray300};
      border-radius: 8px;
      padding: 10px 15px;
      text-align: center;
      box-sizing: border-box;
    }
    input {
      display: none;
      &:checked + label {
        color: ${colors.primary.white};
        background-color: ${colors.primary.blue};
      }
    }

    @media ${device.tablet} {
      flex-direction: row;
    }
  }
`;
