import * as React from 'react';
import 'slick-carousel/slick/slick.css';
import 'slick-carousel/slick/slick-theme.css';
import Layout from '../components/common/Layout/Layout';
import {
  GatsbySeoComponent,
  MetaData,
  TopLevelPageJSONLDSchemas,
} from '../components/common/SEO';
import { StaticPageFrontmatter } from '../types/StaticPageFrontmatter';
import {
  CtaBlock,
  FeatureComparisonSection,
  HeroSection,
  QuotesSection,
} from '../components/telgorithm-vs-twilio';
import { Container } from '../components/common/Container/Container';
import Partners from '../components/telgorithm-vs-twilio/Partners';
import { Text } from '../components/common';
import { StaticImage } from 'gatsby-plugin-image';
import {
  CalculatorContainer,
  CtaBlockBody,
} from '../components/telgorithm-vs-twilio/styled';
import { BulletList, SectionContainer, StyledOl } from '../components/common/styled';
import { PrimaryButton } from '../components/common/Buttons/Buttons';
import { Link } from 'gatsby';
import { ExternalLinks, InternalLinks } from '../utils/links';
import { colors } from '../utils/colors';
import SmartQueueingCalculator from '../components/smart-queueing/Calculator';
import { PaygTrialBanner } from '../components/common/PaygBanner';
import { TalkToAnExpertForm } from '../components/telgorithm-vs-twilio/TalkToAnExpertForm';
import FAQ from '../components/common/FAQ';
import DesktopLogoImage from '../images/Logo/Telgorithm/DesktopLogo.svg';
import featureImage from '../images/TelgorithmVsTwilio/featureImage.png';

const faqItems = [
  {
    question: `Who is Telgorithm?`,
    answer: `Telgorithm is a messaging API provider specializing in A2P 10DLC text messaging. Telgorithm enables software providers to send high-volume business text messages at the highest delivery rates in the industry, 99%+ on average. Telgorithm combines timely customer support, expert-level messaging compliance guidance and patent-pending technology that proactively and automatically manages business’ Carrier rate limits for the most effective 10DLC API experience.`,
  },
  {
    question: `What is Telgorithm’s minimum monthly messaging volume?`,
    answer: `At this time we require new customers to commit to a minimum of 500K messages per month.`,
  },
  {
    question: `Does Telgorithm offer a trial?`,
    answer: (
      <div>
        <p>
          <Text.Regular.Normal.md>
            Yes! As a Campaign Service Provider (CSP), you can sign up for a 30-day
            pay-as-you-go (PAYG) trial to test out Telgorithm’s SMS API technology and
            services. During the trial you will experience Telgorithm’s:
          </Text.Regular.Normal.md>
        </p>
        <br />
        <BulletList>
          <li>
            <Text.Regular.Normal.md>
              Best-in-class customer support
            </Text.Regular.Normal.md>
          </li>
          <li>
            <Text.Regular.Normal.md>
              Expert 10DLC compliance guidance
            </Text.Regular.Normal.md>
          </li>
          <li>
            <Text.Regular.Normal.md>
              Fast Campaign approval timelines
            </Text.Regular.Normal.md>
          </li>
          <li>
            <Text.Regular.Normal.md>
              Unique tech solutions for 10DLC messaging
            </Text.Regular.Normal.md>
          </li>
        </BulletList>
        <br />
        <p>
          <Text.Regular.Normal.md>
            Learn more <Link to={InternalLinks.PaygTrial}>here</Link>.
          </Text.Regular.Normal.md>
        </p>
      </div>
    ),
  },
  {
    question: `How long does it take to integrate with Telgorithm’s SMS APIs?`,
    answer: (
      <div>
        <p>
          <Text.Regular.Normal.md>
            Our SMS APIs are developer-friendly and easy to integrate with.{' '}
            <Link to={InternalLinks.GettingStarted}>Integration</Link> is a 3-step
            process:
          </Text.Regular.Normal.md>
        </p>
        <br />
        <StyledOl>
          <li>
            <Text.Regular.Normal.md>
              You must register or be registered as a Campaign Service Provider (CSP) with
              The Campaign Registry (TCR).
            </Text.Regular.Normal.md>
          </li>
          <li>
            <Text.Regular.Normal.md>
              Once Telgorithm is selected as your Connectivity Partner (CNP) in TCR, you
              can integrate with Telgorithm with minimal coding. Telgorithm will
              automatically import your approved Campaigns into your Telgorithm account.
              You can now purchase numbers.
            </Text.Regular.Normal.md>
          </li>
          <li>
            <Text.Regular.Normal.md>
              Test inbound and outbound text messages, get any questions answered by our
              technical or support teams, and experience the Telgorithm magic!
            </Text.Regular.Normal.md>
          </li>
        </StyledOl>
      </div>
    ),
  },
  {
    question: `Can I use existing numbers to send SMS?`,
    answer: (
      <Text.Regular.Normal.md>
        Yes! Telgorithm is a{' '}
        <Link to={InternalLinks.Glossary + 'hosted-sms-messaging/'}>Hosted SMS</Link>{' '}
        provider which means you can send and receive SMS and MMS on existing numbers
        without having to change your voice provider.
      </Text.Regular.Normal.md>
    ),
  },
];

const IndexPage = () => {
  const title =
    'Twilio SMS Alternative | 10DLC Pricing & TCR Registration Info | Telgorithm';

  return (
    <>
      <MetaData
        title={title}
        featureImage={featureImage}
        description="Looking for a Twilio SMS alternative or need support with Twilio 10DLC registration? Explore Telgorithm for better 10DLC deliverability and support."
      />
      <GatsbySeoComponent
        canonicalUrl="https://www.telgorithm.com/telgorithm-vs-twilio/"
        title={title}
      />
      <TopLevelPageJSONLDSchemas url="https://www.telgorithm.com/telgorithm-vs-twilio/" />
      <Layout disabledContainer>
        <HeroSection />
        <Partners />
        <SectionContainer>
          <Container>
            <CtaBlock
              title="What makes Telgorithm different?"
              largeTitle
              image={
                <StaticImage
                  src="../images/TelgorithmVsTwilio/manWithLaptop.png"
                  alt="Man with laptop"
                  placeholder="none"
                  objectFit="contain"
                ></StaticImage>
              }
              imagePosition="left"
            >
              <CtaBlockBody>
                <p>
                  <Text.Regular.Normal.md>
                    Like Twilio, Telgorithm offers messaging APIs to enable 10DLC texting
                    for your software or application users. The difference is that
                    Telgorithm specializes in A2P 10DLC messaging and we provide
                    professional services including compliance guidance and hands-on
                    support to all of our customers. 
                  </Text.Regular.Normal.md>
                </p>
                <p>
                  <Text.Regular.Normal.md>
                    So if you haven’t been happy with Twilio 10DLC – explore how
                    Telgorithm is the best alternative to handle your 10DLC messaging.
                  </Text.Regular.Normal.md>
                </p>
              </CtaBlockBody>
            </CtaBlock>
          </Container>
        </SectionContainer>
        <SectionContainer>
          <Container>
            <FeatureComparisonSection />
          </Container>
        </SectionContainer>
        <SectionContainer>
          <Container>
            <CalculatorContainer>
              <Text.Title.Bold.lg>
                How much would you save with<img src={DesktopLogoImage} alt="Telgorithm" height={30}/>10DLC Deliverability
                Technology?
              </Text.Title.Bold.lg>
              <SmartQueueingCalculator withoutDescription></SmartQueueingCalculator>
            </CalculatorContainer>
          </Container>
        </SectionContainer>
        <SectionContainer>
          <Container>
            <CtaBlock
              title="Pricing"
              image={
                <StaticImage
                  src="../images/TelgorithmVsTwilio/coworkersWithLaptop.png"
                  alt="Coworkers"
                  placeholder="none"
                  objectFit="contain"
                ></StaticImage>
              }
              imagePosition="left"
            >
              <CtaBlockBody>
                <p>
                  <Text.Regular.Normal.md>
                    Our pricing starts at{' '}
                    <Text.Regular.Bold.md color={colors.secondary.orange}>
                      $0.005
                    </Text.Regular.Bold.md>{' '}
                    with custom pricing options for committed high volume messaging. We do
                    not charge for:
                  </Text.Regular.Normal.md>
                </p>
                <BulletList>
                  <li>
                    <Text.Regular.Normal.md>Inbound SMS messages</Text.Regular.Normal.md>
                  </li>
                  <li>
                    <Text.Regular.Normal.md>Number Porting</Text.Regular.Normal.md>
                  </li>
                  <li>
                    <Text.Regular.Normal.md>Customer Support</Text.Regular.Normal.md>
                  </li>
                  <li>
                    <Text.Regular.Normal.md>
                      Deliverability Technology
                    </Text.Regular.Normal.md>
                  </li>
                </BulletList>
                <p>
                  <Text.Regular.Normal.md>
                    As a full-service messaging API provider, all are included in your
                    per-message service.
                  </Text.Regular.Normal.md>
                </p>
                <Link to={InternalLinks.Pricing}>
                  <PrimaryButton>See Pricing</PrimaryButton>
                </Link>
              </CtaBlockBody>
            </CtaBlock>
          </Container>
        </SectionContainer>
        <QuotesSection />
        <SectionContainer>
          <Container>
            <CtaBlock
              title="Dev Resources"
              image={
                <StaticImage
                  src="../images/TelgorithmVsTwilio/jsonSamples.png"
                  alt="API Json samples"
                  placeholder="none"
                  objectFit="contain"
                ></StaticImage>
              }
              imagePosition="right"
            >
              <CtaBlockBody>
                <p>
                  <Text.Regular.Normal.md>
                    Telgorithm provides extensive developer-friendly documentation. Plus,
                    we utilize{' '}
                    <a
                      href={
                        ExternalLinks.Developers +
                        'docs/integration#overview--generating-telgorithms-messaging-client-using-openapi-generator-online'
                      }
                      target="_blank"
                    >
                      OpenAPI
                    </a>
                    , a standardized specification that defines and documents APIs. In
                    essence, OpenAPI serves as a clear blueprint for our API's structure,
                    endpoints, and data formats. An exciting advantage of OpenAPI is its
                    capacity to assist you in effortlessly generating client libraries for
                    various programming languages.
                  </Text.Regular.Normal.md>
                </p>
                <a
                  href={
                    ExternalLinks.Developers +
                    'docs/integration#overview--migration-from-twilio'
                  }
                  target="_blank"
                >
                  <PrimaryButton>See API Docs</PrimaryButton>
                </a>
              </CtaBlockBody>
            </CtaBlock>
          </Container>
        </SectionContainer>
        <Container>
          <PaygTrialBanner />
        </Container>
        <SectionContainer>
          <Container>
            <FAQ faqItems={faqItems} />
          </Container>
        </SectionContainer>
        <TalkToAnExpertForm />
      </Layout>
    </>
  );
};

export default IndexPage;

export const frontmatter: StaticPageFrontmatter = {
  title: 'Twilio SMS Alternative | 10DLC Pricing & TCR Registration Info',
  metaTitle:
    'The only messaging API provider to guarantee message deliverability. Upgrade your A2P messaging experience and ignite your ROI with Telgorithm.',
  excerpt: 'Telgorithm is the best Twilio SMS alternative for 10DLC.',
  slug: '/telgorithm-vs-twilio/',
};
