import styled from "styled-components";
import { colors } from "../../../utils/colors";
import { device } from "../../../utils/layoutBreakpoints";

export const StyledPartners = styled.section`
  padding-top: 12px;
  text-align: center;

  @media ${device.laptop} {
    padding-top: 10px;
  }

  border-bottom: 1px solid ${colors.secondary.gray200};
`;

export const PartnersContent = styled.div`
  display: flex;
  align-items: center;
  flex-direction: row;
  justify-content: space-between;
  border-radius: 8px;
  padding: 16px;
  overflow: hidden;
  margin: 0 auto;
  opacity: 0.5;
  gap: 25px;

  & > h2,
  & > div {
    transition: all 1s cubic-bezier(0.3, 1.5, 0.66, 1);
  }

  &.hide-logos {
    & > h2,
    & > div {
      opacity: 0;
      transform: translateX(80px);
    }
  }

  & > div:nth-child(2) {
    transition-delay: 0.125s;
    img {
      max-height: 26px;
    }
  }
  & > div:nth-child(3) {
    transition-delay: 0.25s;
    img {
      max-height: 20px;
    }
  }
  & > div:nth-child(4) {

    transition-delay: 0.375s;
    img {
      max-height: 24px;
    }
  }
  & > div:nth-child(5) {
    transition-delay: 0.5s;
    img {
      max-height: 18px;
    }
  }

  @media ${device.tablet} {
    width: 80%;
    margin: 0 auto;
  }

  @media ${device.laptop} {
    max-width: unset;
    width: 70%;
    justify-content: space-between;
    flex-direction: row;
    flex-wrap: no-wrap;
    height: 64px;
    padding: 10px 0;

    & > div:nth-child(2) {
      img {
        max-height: 38px;
      }
    }
    & > div:nth-child(3) {
      img {
        max-height: 32px;
      }
    }
    & > div:nth-child(4) {
      img {
        max-height: 40px;
      }
    }
    & > div:nth-child(5) {
      img {
        max-height: 26px;
      }
    }
  }

  @media ${device.desktop} {
    width: 65%;
  }
`;

export const ImageWrapper = styled.div`
  width: 45%;
  display: flex;
  align-items: flex-end;
  justify-content: center;
  padding: 20px 0 0 0;
  img {
    max-height: 50px;
    user-select: none;
    object-fit: contain;
  }

  @media ${device.laptop} {
    width: auto;
    padding: 0;
  }
`;
