import { StaticImage } from "gatsby-plugin-image";
import React, { useState, useEffect } from "react";
import { Container } from "../../common/Container/Container";
import { ImageWrapper } from "../styled";
import { StyledPartners, PartnersContent } from "./styled";

export const Partners = () => {

  const [animationClass, setAnimationClass] = useState<string>('hide-logos');
  useEffect(() => {
    const timer = setTimeout(() => {
      setAnimationClass('');
    }, 100);

    return () => {
      clearTimeout(timer);
    };
  }, []);

  return (
    <StyledPartners>
      <Container>
        <PartnersContent className={animationClass} >
          <ImageWrapper>
            <StaticImage
              src="../../../images/Logo/Clients/Constant-Contact.png"
              alt="Constant Contact logo"
              placeholder="none"
              objectFit="contain"
              height={38}
            ></StaticImage>
          </ImageWrapper>
          <ImageWrapper>
            <StaticImage
              src="../../../images/Logo/Clients/Boulevard.png"
              alt="Boulevard logo"
              placeholder="none"
              objectFit="contain"
              height={32}
            ></StaticImage>
          </ImageWrapper>
          <ImageWrapper>
            <StaticImage
              src="../../../images/Logo/Clients/Riptide.png"
              alt="Riptide logo"
              placeholder="none"
              objectFit="contain"
              height={30}
            ></StaticImage>
          </ImageWrapper>
          <ImageWrapper>
            <StaticImage
              src="../../../images/Logo/Clients/Walla.png"
              alt="Walla logo"
              placeholder="none"
              objectFit="contain"
              height={26}
            ></StaticImage>
          </ImageWrapper>
        </PartnersContent>
      </Container>
    </StyledPartners>
  );
};

export default Partners;
